import React from "react";
import AliceCarousel from 'react-alice-carousel';
import 'react-alice-carousel/lib/alice-carousel.css';
import img1 from '../images/packages.png'

import './Carousal.css'
const handleDragStart = (e) => e.preventDefault();

const items = [
  <img src={img1} height ={150}  onDragStart={handleDragStart} role="presentation" />,

 
];

const Package = () => {
  return (
    <div style={{width:'50%'}}>
<h1>Other Packages</h1>
<AliceCarousel  autoHeight={false} autoWidth={false} items={items} infinite={true} responsive={
       { 700: {
            items: 1,
        },
        1024: {
            items: 1
        }}
      }/>
    </div>
   
  );
}
 
export default Package;