import React from "react";
import AliceCarousel from 'react-alice-carousel';

import 'react-alice-carousel/lib/alice-carousel.css';
import img1 from '../images/maths.png'
import img2 from '../images/english.png'
import questions from "../Pages/Questions";
import './Carousal.css'
import {
  Link
} from "react-router-dom";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
const handleDragStart = (e) => e.preventDefault();

const items = [
    <img src={img1} onClick={()=>{
      <Link to="../Pages/Questions"></Link>
      console.log("hello");
    }}  onDragStart={handleDragStart} role="presentation" />
,
  <img src={img2}  onDragStart={handleDragStart} role="presentation" />
  ,
    <img src={img1}   onDragStart={handleDragStart} role="presentation" />
,
  <img src={img2}  onDragStart={handleDragStart} role="presentation" />
  ,   <img src={img1}   onDragStart={handleDragStart} role="presentation" />
  ,
    <img src={img2}  onDragStart={handleDragStart} role="presentation" />
    ,
 
];

const Gallery = () => {
  return (
    <AliceCarousel  autoHeight={false} autoWidth={false} items={items} infinite={true} responsive={
      { 700: {
           items: 1,
       },
       1024: {
           items: 5
       }}
     }/>
   
  );
}
 export default Gallery;